import React, { useState, useEffect, useRef } from 'react';
import classNames from 'classnames/bind';
import styles from '../Styles/Admin.module.scss';
import { Editor } from '@tinymce/tinymce-react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faAngleDown } from '@fortawesome/free-solid-svg-icons';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import Header from '../Components/Header';
import Footer from '../Components/Footer';
import ModalDeletePro from '../utils/Modal/DeleteProduct';
import ModalUpdatePro from '../utils/Modal/ModalUpdatePro';
import request from '../Config/api';
import ModalEditOrder from '../utils/Modal/ModalEditOrder';
import ManageProducts from '../Components/ManageProducts';
import ManageOrder from '../Components/ManageOrder';
import ChartLine from '../Components/ChartLine';

const cx = classNames.bind(styles);

const Admin = () => {
    const [nameProduct, setNameProduct] = useState('');
    const [priceProduct, setPriceProduct] = useState(0);
    const [description, setDescription] = useState('');
    const [fileImg, setFileImg] = useState([]);
    const [checkType, setCheckType] = useState(0);
    const [dataCart, setDataCart] = useState([]);
    const [dataProduct, setDataProduct] = useState([]);
    const [showModalDelete, setShowModalDelete] = useState(false);
    const [showModalUpdate, setShowModalUpdate] = useState(false);
    const [selectedProduct, setSelectedProduct] = useState({});
    const [valueSelect, setValueSelect] = useState(1);
    const [address, setAddress] = useState('');
    const editorRef = useRef(null);

    const [showModal, setShowModal] = useState(false);
    const [idPro, setIdPro] = useState(0);

    const handleShowModalEdit = (id, address1) => {
        setShowModal(!showModal);
        setIdPro(id);
        setAddress(address1);
    };

    useEffect(() => {
        fetchData();
    }, [showModalDelete, showModalUpdate, showModal]);

    const fetchData = async () => {
        try {
            const cartResponse = await request.get('api/getallorder');
            setDataCart(cartResponse.data);

            const productsResponse = await request.get('/api/products');
            setDataProduct(productsResponse.data);
        } catch (error) {
            console.error('Error fetching data:', error);
        }
    };

    const handleEditorChange = () => {
        if (editorRef.current) {
            setDescription(editorRef.current.getContent());
        }
    };

    const handleFileChange = (e) => {
        const filesArray = Array.from(e.target.files);
        const newImg = filesArray.sort((a, b) => a.name.localeCompare(b.name));

        setFileImg(newImg);
    };

    const handleAddProduct = async () => {
        const formData = new FormData();
        formData.append('nameProduct', nameProduct);
        formData.append('priceProduct', priceProduct);
        formData.append('description', description);
        formData.append('checkType', checkType);
        fileImg.forEach((file) => {
            formData.append('fileImg', file);
        });

        try {
            const response = await request.post('/api/addproduct', formData, {
                headers: {
                    'Content-Type': 'multipart/form-data',
                },
            });
            toast.success(response.data.message);
            fetchData();
            clearForm();
        } catch (error) {
            console.error('Error uploading product:', error);
            toast.error('Error uploading product');
        }
    };

    const clearForm = () => {
        setNameProduct('');
        setPriceProduct(0);
        setDescription('');
        setCheckType(0);
        setFileImg([]);
    };

    const handleShowModalDelete = (item) => {
        setSelectedProduct(item);
        setShowModalDelete(true);
    };

    const handleShowModalUpdate = (item) => {
        setSelectedProduct(item);
        setShowModalUpdate(true);
    };

    const handleShowSelect = (e) => {
        setValueSelect(e);
    };

    return (
        <div className={cx('wrapper')}>
            <ToastContainer />

            <header>
                <Header />
            </header>

            <main className={cx('main')}>
                <div className={cx('inner')}>
                    <h1>ng Sn Phm</h1>
                    <div className="form-floating mb-3">
                        <input
                            type="text"
                            className="form-control"
                            id="floatingInput"
                            value={nameProduct}
                            onChange={(e) => setNameProduct(e.target.value)}
                        />
                        <label htmlFor="floatingInput">Name Product</label>
                    </div>
                    <div className="form-floating">
                        <input
                            type="number"
                            className="form-control"
                            id="floatingPassword"
                            value={priceProduct}
                            onChange={(e) => setPriceProduct(e.target.value)}
                        />
                        <label htmlFor="floatingPassword">Price Product</label>
                    </div>
                    <select
                        className="form-select"
                        aria-label="Default select example"
                        value={checkType}
                        onChange={(e) => setCheckType(e.target.value)}
                    >
                        <option value="0">Chn Loi Giy</option>
                        <option value="1">Giy Nam</option>
                        <option value="2">Giy N</option>
                        <option value="3">Giy Tr Em</option>
                    </select>

                    <Editor
                        apiKey="n4hxnmi16uwk9dmdgfx6nscsf8oc30528dlcub1mzsk8deqy"
                        onInit={(evt, editor) => (editorRef.current = editor)}
                        initialValue={description}
                        init={{
                            height: 500,
                            menubar: false,
                            plugins: [
                                'advlist',
                                'autolink',
                                'lists',
                                'link',
                                'image',
                                'charmap',
                                'preview',
                                'anchor',
                                'searchreplace',
                                'visualblocks',
                                'code',
                                'fullscreen',
                                'insertdatetime',
                                'media',
                                'table',
                                'code',
                                'help',
                                'wordcount',
                            ],
                            toolbar:
                                'undo redo | formatselect | ' +
                                'bold italic forecolor | alignleft aligncenter ' +
                                'alignright alignjustify | bullist numlist outdent indent | ' +
                                'removeformat | help',
                            content_style: 'body { font-family:Helvetica,Arial,sans-serif; font-size:14px }',
                        }}
                        onChange={handleEditorChange}
                    />

                    <div className={cx('form-upload-image')}>
                        <div style={{ height: '25px' }}>
                            <FontAwesomeIcon id={cx('icon-animation')} icon={faAngleDown} />
                        </div>
                        <label htmlFor="file-upload">Select Image</label>
                        <input id="file-upload" type="file" name="fileImg" multiple onChange={handleFileChange} />
                        <div className={cx('image-container')}>
                            {fileImg.map((file, index) => (
                                <div key={index} className={cx('image-upload')}>
                                    <img src={URL.createObjectURL(file)} alt="" />
                                </div>
                            ))}
                        </div>
                    </div>

                    <div className={cx('btn-submit')}>
                        <button onClick={handleAddProduct}>Upload</button>
                    </div>

                    <div className={cx('btn-select')}>
                        <div>
                            <button onClick={() => handleShowSelect(1)}>Qun l n hng</button>
                        </div>

                        <div>
                            <button onClick={() => handleShowSelect(2)}>Qun l sn phm </button>
                        </div>

                        <div>
                            <button onClick={() => handleShowSelect(3)}>Qun l doanh thu</button>
                        </div>
                    </div>
                    {valueSelect === 1 ? (
                        <div>
                            <ManageOrder
                                dataCart={dataCart}
                                handleShowModalEdit={handleShowModalEdit}
                                setAddress={setAddress}
                            />
                        </div>
                    ) : (
                        <></>
                    )}
                    {valueSelect === 2 ? (
                        <div>
                            <ManageProducts
                                dataProduct={dataProduct}
                                handleShowModalDelete={handleShowModalDelete}
                                handleShowModalUpdate={handleShowModalUpdate}
                                setIdPro={setIdPro}
                            />
                        </div>
                    ) : (
                        <></>
                    )}
                    {valueSelect === 3 ? (
                        <div>
                            <ChartLine />
                        </div>
                    ) : (
                        <></>
                    )}
                </div>
            </main>

            <footer>
                <Footer />
            </footer>

            <ModalDeletePro show={showModalDelete} setShow={setShowModalDelete} nameProduct={selectedProduct} />
            <ModalUpdatePro show={showModalUpdate} setShow={setShowModalUpdate} data={selectedProduct} />
            <ModalEditOrder show={showModal} setShow={setShowModal} id={idPro} address={address} />
        </div>
    );
};

export default Admin;
