import classNames from 'classnames/bind';
import styles from '../Styles/Login.module.scss';
// import Header from '../Components/Header';
import request from '../Config/api';

import { Link, useNavigate } from 'react-router-dom';
import { useState } from 'react';
import { toast, ToastContainer } from 'react-toastify';

const cx = classNames.bind(styles);

function LoginUser() {
    const [email, setEmail] = useState(''); // To state e luu email
    const [password, setPassword] = useState(''); // To state e luu password
    const navigate = useNavigate(); // To state e luu password
    const handleLoginUser = async () => {
        // Ham xu ly ang nhap
        var pattern = /[A-Z]/; // Kiem tra xem chuoi co chua ky tu viet hoa hay khong
        const test = pattern.test(email);
        if (email === '' || password === '' || test === true) {
            // Kiem tra xem email, password
            toast.error('Vui Lng Xem Li Thng Tin !!!'); // Hm toast.error hien thi thong bao loi
        } else {
            try {
                // Thuc hien ang nhap
                const res = await request.post('/api/login', {
                    // Gui yeu cau ang nhap en server
                    email, // Gui email va password e ang nhap
                    password,
                });
                toast.success(res.data.message); // Hien thi thong bao thanh cong
                navigate('/'); // Chuyen huong en trang chu
            } catch (error) {
                // Neu ang nhap that bai
                toast.error(error.response.data.message); // Hien thi thong bao loi
            }
        }
    };

    return (
        <>
            <ToastContainer />
            {/* <header>
                <Header />
            </header> */}
            <div className={cx('wrapper')}>
                <div className={cx('inner')}>
                    <div className={cx('header-form-login')}>
                        <span>Login</span>
                        <p>Enter Login details to get access</p>
                    </div>
                    <div className={cx('input-box')}>
                        <div className={cx('form-input')}>
                            <label>Username or Email Address</label>
                            <input placeholder="Username / Email address" onChange={(e) => setEmail(e.target.value)} />
                        </div>

                        <div className={cx('form-input')}>
                            <label>Password</label>
                            <input
                                placeholder="Enter Password"
                                type="password"
                                onChange={(e) => setPassword(e.target.value)}
                            />
                        </div>

                        <div className={cx('single-input-fields')}>
                            <div>
                                <input type="checkbox" />
                                <label>Keep me logged in</label>
                            </div>
                            <Link to={'/forgotPassword'}>Forgot Password?</Link>
                        </div>
                    </div>
                    <div className={cx('login-footer')}>
                        <p>
                            Dont have an account?{' '}
                            <Link id={cx('link')} to="/register">
                                Sign Up
                            </Link>{' '}
                            here
                        </p>
                        <button onClick={handleLoginUser}>Login</button>
                    </div>
                </div>
            </div>
        </>
    );
}

export default LoginUser;
