import classNames from 'classnames/bind';
import styles from '../Styles/Login.module.scss';
// import Header from '../Components/Header';
import request from '../Config/api';

import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { Link } from 'react-router-dom';
import { useState } from 'react';

const cx = classNames.bind(styles);

function RegisterUser() {
    const [fullname, setFullname] = useState(''); // To state e luu fullname
    const [email, setEmail] = useState(''); // To state e luu email
    const [phone, setPhone] = useState(''); // To state e luu phone
    const [password, setPassword] = useState(''); // To state e luu password
    const [confirmPassword, setConfirmPassword] = useState(''); // To state e luu confirmPassword

    const handleRegister = async () => {
        // Ham xu ly ang ky
        try {
            // Thuc hien ang ky
            var pattern = /[A-Z]/; // Kiem tra xem chuoi co chua ky tu viet hoa hay khong
            const checkEmail = pattern.test(email);

            if (fullname === '' || email === '' || password === '' || confirmPassword === '') {
                // Kiem tra xem fullname, email, password, confirmPassword
                toast.error('Vui Lng Xem Li Thng Tin !!!'); // Hm toast.error hien thi thong bao loi
            } else if (checkEmail === true) {
                // Kiem tra xem email
                toast.error('Email Khng c Vit Hoa !!!'); // Hm toast.error hien thi thong bao loi
            } else if (password !== confirmPassword) {
                // Kiem tra xem password, confirmPassword
                toast.error('Mt Khu Khng Trng Khp !!!'); // Hm toast.error hien thi thong bao loi
            } else {
                // Neu ang ky thanh cong
                const res = await request.post('/api/register', {
                    // Thuc hien ang ky
                    fullname,
                    email,
                    password,
                    confirmPassword,
                    phone,
                }); // Gui yeu cau ang ky en server
                toast.success(res.data.message); // Hien thi thong bao thanh cong
            }
        } catch (error) {
            // Neu ang ky that bai
            toast.error(error.response.data.message); // Hien thi thong bao loi
        }
    };

    return (
        <>
            <ToastContainer />
            {/* <header>
                <Header />
            </header> */}
            <div style={{ marginTop: '60px' }} className={cx('wrapper')}>
                <div className={cx('inner')}>
                    <div className={cx('header-form-login')}>
                        <span>Sign Up</span>
                        <p>Create your account to get full access</p>
                    </div>
                    <div className={cx('input-box')}>
                        <div className={cx('form-input')}>
                            <label>Full Name</label>
                            <input placeholder="Enter Full Name" onChange={(e) => setFullname(e.target.value)} />
                        </div>

                        <div className={cx('form-input')}>
                            <label>Email Address</label>
                            <input placeholder="Enter Email Address" onChange={(e) => setEmail(e.target.value)} />
                        </div>

                        <div className={cx('form-input')}>
                            <label>Phone</label>
                            <input placeholder="Enter Phone" onChange={(e) => setPhone(e.target.value)} />
                        </div>

                        <div className={cx('form-input')}>
                            <label>Password</label>

                            <input
                                placeholder="Enter Password"
                                type="password"
                                onChange={(e) => setPassword(e.target.value)}
                            />
                        </div>

                        <div className={cx('form-input')}>
                            <label>Confirm Password</label>
                            <input
                                type="password"
                                placeholder="Confirm Password"
                                onChange={(e) => setConfirmPassword(e.target.value)}
                            />
                        </div>
                    </div>
                    <div className={cx('login-footer')}>
                        <p>
                            Already have an account?
                            <Link id={cx('link')} to="/login">
                                Login
                            </Link>
                            here
                        </p>
                        <button onClick={handleRegister}>Sign Up</button>
                    </div>
                </div>
            </div>
        </>
    );
}

export default RegisterUser;
