import classNames from 'classnames/bind';
import styles from '../Styles/Payments.module.scss';
import Header from '../Components/Header';
import Footer from '../Components/Footer';
import axios from 'axios';
import request from '../Config/api';

import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { useEffect, useMemo, useState } from 'react';
import { useNavigate } from 'react-router-dom';

const cx = classNames.bind(styles);

function Payments() {
    const [dataCart, setDataCart] = useState([]);
    const [tinhthanh, setTinhThanh] = useState([]);
    const [idTinhThanh, setIdTinhThanh] = useState(0);
    const [huyen, setHuyen] = useState([]);
    const [idHuyen, setIdHuyen] = useState(0);
    const [xa, setXa] = useState([]);
    const [address, setAddress] = useState('');
    const [paymentMethod, setPaymentMethod] = useState('COD');

    const navigate = useNavigate();

    const totalProduct = useMemo(() => {
        const total = dataCart.reduce((acc, item) => acc + item.price * item.quantity, 0);
        return total;
    }, [dataCart]);

    useEffect(() => {
        axios.get('https://esgoo.net/api-tinhthanh/1/0.htm').then((res) => setTinhThanh(res.data.data));
    }, []);

    useEffect(() => {
        if (idTinhThanh !== 0) {
            axios.get(`https://esgoo.net/api-tinhthanh/2/${idTinhThanh}.htm`).then((res) => setHuyen(res.data.data));
        }
    }, [idTinhThanh]);

    useEffect(() => {
        if (idHuyen !== 0) {
            axios.get(`https://esgoo.net/api-tinhthanh/3/${idHuyen}.htm`).then((res) => setXa(res.data.data));
        }
    }, [idHuyen]);

    const handlePaymentMethodChange = (e) => {
        setPaymentMethod(e.target.value);
    };

    useEffect(() => {
        request.get('/api/cart').then((res) => setDataCart(res.data));
    }, []);

    const handlePayment = async () => {
        if (paymentMethod === 'Momo') {
            const res = await request.post('/api/payment', { dataCart, totalProduct, address });
            window.open(res.data.payUrl);
        } else if (paymentMethod === 'COD') {
            const res = await request.post('/api/paymentcod', { dataCart, totalProduct, address });
            toast.success(res.data.message);
            navigate('/paymentsuccess');
        }
    };

    return (
        <div className={cx('wrapper')}>
            <ToastContainer />
            <header>
                <Header />
            </header>

            <main className={cx('main')}>
                <h2>Thanh ton</h2>
                <div className={cx('form-payments')}>
                    <div className={cx('column-left')}>
                        <h3>THNG TIN THANH TON</h3>
                        <div className={cx('form-1')}>
                            <div className="form-floating mb-3">
                                <input type="text" className="form-control" id="floatingInput" />
                                <label htmlFor="floatingInput">H v tn *</label>
                            </div>
                            <div className="form-floating">
                                <input type="text" className="form-control" id="floatingPassword" />
                                <label htmlFor="floatingPassword">S in thoi *</label>
                            </div>
                        </div>

                        <div>
                            <div className="form-floating mb-3">
                                <input type="text" className="form-control" id="floatingInput" />
                                <label htmlFor="floatingInput">a ch email *</label>
                            </div>
                            <select
                                className="form-select"
                                aria-label="Default select example"
                                selected
                                onChange={(e) => setIdTinhThanh(e.target.value)}
                            >
                                <option value="0">Tnh/Thnh</option>
                                {tinhthanh.map((item) => (
                                    <option key={item.id} value={item.id}>
                                        {item.name}
                                    </option>
                                ))}
                            </select>
                        </div>

                        <div>
                            <select
                                onChange={(e) => setIdHuyen(e.target.value)}
                                className="form-select mt-3"
                                aria-label="Default select example"
                            >
                                <option value="0">Qun/Huyn</option>
                                {huyen.map((item) => (
                                    <option key={item.id} value={item.id}>
                                        {item.name}
                                    </option>
                                ))}
                            </select>

                            <select className="form-select mt-3" aria-label="Default select example">
                                <option value="0">X/Phng/Th trn</option>
                                {xa.map((item) => (
                                    <option key={item.id} value={item.id}>
                                        {item.name}
                                    </option>
                                ))}
                            </select>
                        </div>

                        <div>
                            <div className="form-floating mt-3">
                                <input
                                    type="text"
                                    className="form-control"
                                    id="floatingInput"
                                    onChange={(e) => setAddress(e.target.value)}
                                />
                                <label htmlFor="floatingInput">a Ch *</label>
                            </div>
                        </div>

                        <div className="form-floating mt-3">
                            <textarea
                                style={{ height: '100px' }}
                                className="form-control"
                                placeholder="Leave a comment here"
                                id="floatingTextarea"
                            ></textarea>
                            <label htmlFor="floatingTextarea">Ghi Ch n Hng</label>
                        </div>

                        <div className={cx('select-payment')}>
                            <h4>PHNG THC THANH TON</h4>
                            <div className="form-check">
                                <input
                                    className="form-check-input"
                                    type="radio"
                                    name="paymentMethod"
                                    value="COD"
                                    id="flexRadioDefault1"
                                    onChange={handlePaymentMethodChange}
                                    checked={paymentMethod === 'COD'}
                                />
                                <label className="form-check-label" htmlFor="flexRadioDefault1">
                                    Thanh Ton Khi Nhn Hng
                                </label>
                            </div>
                            <div className="form-check">
                                <input
                                    className="form-check-input"
                                    type="radio"
                                    name="paymentMethod"
                                    value="Momo"
                                    id="flexRadioDefault2"
                                    onChange={handlePaymentMethodChange}
                                    checked={paymentMethod === 'Momo'}
                                />
                                <label className="form-check-label" htmlFor="flexRadioDefault2">
                                    Thanh Ton Qua Momo
                                </label>
                            </div>
                            <div onClick={handlePayment} className={cx('btn-payment')}>
                                <button id={cx('btn-buy')}>Hon Tt n Hng</button>
                            </div>
                        </div>

                        <div></div>
                    </div>
                    <div className={cx('total-product')}>
                        <h3>TNG CNG | {dataCart.length} SN PHM</h3>
                        <div>
                            <table className="table table-bordered border-primary">
                                <thead>
                                    <tr>
                                        <th scope="col">Tm tnh</th>
                                        <th scope="col">{totalProduct.toLocaleString()}</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr>
                                        <td>Ph Vn Chuyn</td>
                                        <td>Min ph vn chuyn</td>
                                    </tr>
                                    <tr>
                                        <td>Tng Cng</td>
                                        <th>{totalProduct.toLocaleString()}</th>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                        <div className={cx('img-product')}>
                            <h4>SN PHM  T HNG</h4>
                            <div className={cx('img')}>
                                {dataCart.map((item) => (
                                    <img
                                        key={item.id}
                                        src={`http://localhost:5000/uploads/${item.img}`}
                                        alt={item.name}
                                    />
                                ))}
                            </div>
                        </div>
                    </div>
                </div>
            </main>

            <footer>
                <Footer />
            </footer>
        </div>
    );
}

export default Payments;
