import classNames from 'classnames/bind';
import styles from '../Styles/Cart.module.scss';

import Header from '../Components/Header';
import Footer from '../Components/Footer';
import request from '../Config/api';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faClose } from '@fortawesome/free-solid-svg-icons';
import { Link } from 'react-router-dom';
import { useEffect, useMemo, useState } from 'react';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

const cx = classNames.bind(styles);

function Cart() {
    const [dataCart, setDataCart] = useState([]);

    const token = document.cookie;

    const totalProduct = useMemo(() => {
        const total = dataCart?.reduce((acc, item) => acc + item?.price * item?.quantity, 0);
        return total;
    }, [dataCart]);

    const handleDeleteCart = async (id) => {
        await request.post('/api/deletecart', { id }).then((res) => toast.success(res.data.message));
        await request.get('/api/cart').then((res) => setDataCart(res.data));
    };

    useEffect(() => {
        if (!token) {
            return;
        }
        request.get('/api/cart').then((res) => setDataCart(res.data));
    }, []);

    return (
        <div className={cx('wrapper')}>
            <ToastContainer />
            <header>
                <Header />
            </header>

            <main className={cx('main')}>
                <h2>Gi Hng</h2>
                <div className={cx('inner')}>
                    {dataCart.length > 0 ? (
                        <div>
                            {dataCart.map((item) => (
                                <div key={item?.id} className={cx('cart-products')}>
                                    <div className={cx('img-product')}>
                                        <img src={`http://localhost:5000/uploads/${item?.img}`} alt="" />
                                    </div>

                                    <div className={cx('info-product')}>
                                        <h2>{item?.nameProduct}</h2>

                                        <span style={{ fontSize: '17px', fontWeight: '700' }}>
                                            S Lng: x{item?.quantity}
                                        </span>
                                        <span id={cx('price')}>
                                            {(item?.price * item?.quantity).toLocaleString()} 
                                        </span>
                                    </div>

                                    <div className={cx('remove-product')}>
                                        <button onClick={() => handleDeleteCart(item?._id)}>
                                            <FontAwesomeIcon icon={faClose} />
                                        </button>
                                    </div>
                                </div>
                            ))}
                        </div>
                    ) : (
                        <div className={cx('no-product')}>
                            <img src="https://static.topcv.vn/v4/image/job-list/none-result.png" alt="" />
                            <span>Bn Cha C Sn Phm No</span>
                        </div>
                    )}

                    <div className={cx('total-product')}>
                        <h3>TNG CNG | {dataCart.length} SN PHM</h3>
                        <div>
                            <table className="table table-bordered border-primary">
                                <thead>
                                    <tr>
                                        <th scope="col">Tm tnh</th>
                                        <th scope="col">{totalProduct.toLocaleString()} </th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr>
                                        <td>Ph Vn Chuyn</td>
                                        <td>Min ph vn chuyn</td>
                                    </tr>
                                    <tr>
                                        <td>Tng Cng</td>
                                        <th>{totalProduct.toLocaleString()} </th>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                        <div className={cx('btn-total')}>
                            <button id={cx('btn-buy')}>
                                <Link to={'/payments'}>Tin hnh thanh ton</Link>
                            </button>
                            <button id={cx('btn-continue')}>
                                <Link to={'/category'}>Tip tc mua sm</Link>
                            </button>
                        </div>
                    </div>
                </div>
            </main>

            <footer>
                <Footer />
            </footer>
        </div>
    );
}

export default Cart;
