import { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import request from '../../Config/api';

import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

function ModalEditOrder({ show, setShow, id, address }) {
    const handleClose = () => setShow(false);

    const [valueOption, setValueOption] = useState(0);

    const handleEditOrder = () => {
        request.post('/api/editorder', { valueOption, id }).then((res) => {
            toast.success(res.data.message);
        });
    };

    return (
        <>
            <Modal show={show} onHide={handleClose}>
                <Modal.Header closeButton>
                    <Modal.Title>Chnh Sa Sn Phm</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <span>Bn mun gi n hng n : {address}</span>
                    <select
                        class="form-select mt-3"
                        aria-label="Default select example"
                        onChange={(e) => setValueOption(e.target.value)}
                    >
                        <option selected>Chn Tnh Trng</option>
                        <option value="1">ang vn chuyn</option>
                        <option value="2"> Giao Hng</option>
                    </select>
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={handleClose}>
                        ng
                    </Button>
                    <Button variant="primary" onClick={handleEditOrder}>
                        Lu li
                    </Button>
                </Modal.Footer>
            </Modal>
        </>
    );
}

export default ModalEditOrder;
