import classNames from 'classnames/bind';
import styles from '../Styles/ManageOrder.module.scss';

const cx = classNames.bind(styles);

function ManageOrder({ dataCart, handleShowModalEdit }) {
    return (
        <div className={cx('manage-product')}>
            <h2>Qun L n Hng</h2>
            <div className="table-responsive">
                <table className="table table-bordered border-primary ">
                    <thead>
                        <tr>
                            <th scope="col">Ngi Dng</th>
                            <th scope="col">Tn n Hng</th>
                            <th scope="col">a Ch </th>
                            <th scope="col">Size</th>
                            <th scope="col">S Lng</th>
                            <th scope="col">Tnh Trng</th>
                            <th scope="col">Hnh ng</th>
                        </tr>
                    </thead>
                    <tbody>
                        {dataCart.map((item) => (
                            <tr key={item._id}>
                                <td>{item.user}</td>
                                <td>{item.nameProduct}</td>
                                <td>{item.address}</td>
                                <td>{item.size}</td>
                                <td>{item.quantity}</td>
                                <td>{item.tinhtrang ? ' Giao Thnh Cng ' : 'Ngi Bn ang Chun B Hng'}</td>
                                <td style={{ display: 'flex', flexDirection: 'column', gap: '10px' }}>
                                    <button
                                        onClick={() => handleShowModalEdit(item._id, item.address)}
                                        type="button"
                                        className="btn btn-primary"
                                    >
                                        Xc Nhn n Hng
                                    </button>
                                </td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            </div>
        </div>
    );
}

export default ManageOrder;
