import classNames from 'classnames/bind';
import styles from '../Styles/PaymentsSuccess.module.scss';
import Header from '../Components/Header';
import Footer from '../Components/Footer';
import { useEffect, useState } from 'react';
import request from '../Config/api';

const cx = classNames.bind(styles);

function PaymentSuccess() {
    const [dataPayment, setDataPayment] = useState([]);

    const token = document.cookie;

    useEffect(() => {
        if (!token) {
            return;
        }
        request.get('/api/payment').then((res) => setDataPayment(res.data));
    }, []);

    const now = new Date();

    // Ly ra cc thnh phn ca ngy gi hin ti
    const year = now.getFullYear();
    const month = now.getMonth() + 1; // Lu : Thng trong JavaScript bt u t 0
    const date = now.getDate();

    // nh dng ngy gi theo  mun
    const formattedDate = `${date}/${month}/${year}`;

    return (
        <div className={cx('wrapper')}>
            <header>
                <Header />
            </header>

            <main className={cx('main')}>
                <div className={cx('success-animation')}>
                    <svg className={cx('checkmark')} xmlns="http://www.w3.org/2000/svg" viewBox="0 0 52 52">
                        <circle className={cx('checkmark__circle')} cx="26" cy="26" r="25" fill="none" />
                        <path className={cx('checkmark__check')} fill="none" d="M14.1 27.2l7.1 7.2 16.7-16.8" />
                    </svg>
                </div>
                {dataPayment.map((item) => (
                    <div key={item?.id} className={cx('content')}>
                        <h2>Thanh Ton Thnh Cng </h2>
                        <div className={cx('info')}>
                            <h3>Cm n Qu Khch!</h3>
                            <span>
                                Chng ti xin thng bo rng thanh ton ca qu khch  c x l thnh cng. Cm n
                                qu khch  tin tng v s dng dch v ca chng ti.
                            </span>
                            <ul>
                                Thng tin thanh ton
                                <li>Ngy thanh ton: {formattedDate}</li>
                                <li>
                                    Phng thc thanh ton:{' '}
                                    {item?.trangthai ? ' Thanh Ton' : 'Thanh Ton Khi Nhn Hng'}
                                </li>
                            </ul>
                            <span>
                                Qu khch c th kim tra chi tit n hng v theo di trng thi vn chuyn trong ti
                                khon ca mnh trn trang web ca chng ti.
                            </span>
                            <span>
                                Nu qu khch c bt k cu hi hoc cn h tr thm, xin vui lng lin h vi i ng
                                chm sc khch hng ca chng ti qua email [a ch email chm sc khch hng] hoc gi
                                in thoi ti s [S in thoi chm sc khch hng].
                            </span>
                        </div>
                    </div>
                ))}
            </main>

            <footer>
                <Footer />
            </footer>
        </div>
    );
}

export default PaymentSuccess;
