import classNames from 'classnames/bind';
import styles from '../Styles/ManageProducts.module.scss';
import Pagination from './Pagination';

import React, { useState } from 'react';

const cx = classNames.bind(styles);

function ManageProducts({ dataProduct, handleShowModalDelete, handleShowModalUpdate, setIdPro }) {
    const [page, setPage] = useState(1);
    const productsPerPage = 10;
    const startIndex = (page - 1) * productsPerPage;
    const totalPages = Math.ceil(dataProduct.length / productsPerPage);
    const currentProducts = dataProduct.slice(startIndex, startIndex + productsPerPage);

    const handlePageChange = (event, value) => {
        setPage(value);
    };
    return (
        <div>
            <div className={cx('manage-product')}>
                <h2 style={{ fontSize: '23px', fontWeight: '700', marginTop: '50px' }}>Qun L Sn Phm</h2>
                <div className="table-responsive">
                    <table className="table table-bordered border-primary">
                        <thead>
                            <tr>
                                <th scope="col">nh</th>
                                <th scope="col">Tn Sn Phm</th>
                                <th scope="col">Loi Giy</th>
                                <th scope="col">Gi Sn Phm</th>
                                <th scope="col">Hnh ng</th>
                            </tr>
                        </thead>
                        <tbody>
                            {currentProducts.map((item) => (
                                <tr key={item._id}>
                                    <td>
                                        <img
                                            style={{ width: '80px' }}
                                            src={`http://localhost:5000/uploads/${item.img[0]}`}
                                            alt=""
                                        />
                                    </td>
                                    <td>{item.name}</td>
                                    <td>
                                        {item.type === 1 ? 'Giy Nam' : item.type === 2 ? 'Giy N' : 'Giy Tr Em'}
                                    </td>
                                    <td>{item.price.toLocaleString()} </td>
                                    <td>
                                        <button className="btn btn-danger" onClick={() => handleShowModalDelete(item)}>
                                            Xa
                                        </button>
                                        <button
                                            className={cx('btn-update', 'btn', 'btn-warning')}
                                            onClick={() => handleShowModalUpdate(item)}
                                        >
                                            Sa
                                        </button>
                                    </td>
                                </tr>
                            ))}
                        </tbody>
                    </table>
                    <div className={cx('pagination')}>
                        <Pagination totalPages={totalPages} page={page} handlePageChange={handlePageChange} />
                    </div>
                </div>
            </div>
        </div>
    );
}

export default ManageProducts;
